body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: #daa520;
  text-decoration: none;
}

.cursor-pointer {
  cursor: pointer;
}

.header {
  top: 0px;
  z-index: 999;
  position: sticky;
  width: 100%;
  color: #f5f5f5;
  height: 70px;
  background: #24242c;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 5px 5px rgba(0, 0, 0, 0.22);
}

.header__title {
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.header__sub-title {
  padding-left: 1px;
  color: #daa520;
}

.header__with-api {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
}

.authorization-form-modal .modal-title {
  width: 100%;
  text-align: center;
}

.authorization-form-modal .modal-body {
  padding: 1rem 2.5rem;
}

.authorization-form-title {
  font-size: 22px;
  text-align: center;
  margin-bottom: 10px;
}

.invalid-field {
  color: red;
  padding-left: 5px;
}

.sort {
  padding: 15px;
  border: 1px solid #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
  background: #fff;
}

.filters-label {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 13px;
}

.movie-item {
  width: 200px;
  height: 375px;
  flex-direction: column;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%);
  margin-bottom: 20px;
  border-radius: 0 0 5px 5px;
  background: #fff;
}

.movie-item .poster-image .image {
  width: 200px;
  height: 300px;
  object-fit: cover;
  border-radius: 5px 5px 0px 0px;
}

.movie-item .desc {
  position: relative;
  width: 100%;
  height: 75px;
  padding: 0 10px;
}

.movie-item .vote-avarage {
  position: absolute;
  margin: 0;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  top: -22px;
  left: 17px;
  color: #fff;
  box-sizing: border-box;
  padding: 0;
  border-radius: 100%;
  background: #24242c;
  font-size: 15px;
}

.movie-item .title {
  position: relative;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 700;
  padding-top: 15px;
  margin-bottom: 0;
}

.movie-item .release-date {
  position: relative;
  width: 100%;
  font-size: 15px;
}

.pagination .total-pages {
  background: #e2e6ea;
}

.filters-second-label {
  display: block;
  font-size: 16px;
  font-weight: 500;
}

.reset-filters {
  background: #e2e6ea;
}

.genres__item .label {
  position: relative;
  top: -1.5px;
  margin-left: 6px;
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 2px;
}

@media (max-width: 768px) {
  .header {
    height: auto;
    padding: 10px 0px;
  }
}

@media (max-width: 350px) {
  .header__with-api {
    display: none;
  }
}
